___CSS_LOADER_EXPORT___.push([module.id, `ion-button {
  --box-shadow: transparent !important;
}

button {
  background-color: transparent !important;
  border: none;
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
}

ion-searchbar {
  --icon-color: #1ca3c9;
  width: 95%;
  margin-left: 10px;
}

.search-container {
  width: 90vw;
  right: 5vw;
  height: auto;
  max-height: 50vh;
  overflow-y: scroll;
  position: absolute;
  z-index: 99;
}

.line-filter {
  text-align: center;
  line-height: 20px;
  background: #70c2d1;
  padding: 0;
  margin: 0;
}

.header-ios ion-toolbar:last-of-type {
  --border-width: 0;
}

.header-translucent-ios ion-toolbar {
  --opacity: 1;
}

ion-card-title {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

ion-card-content {
  width: 100%;
  height: auto;
  overflow: hidden;
  text-overflow: ellipsis;
}

.textito {
  height: 150px;
}

.textis {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  margin: 1em 0em;
}
.textis img {
  width: 40%;
  height: auto;
}
.textis span {
  font-size: 1.1em;
  font-weight: 600;
  margin-top: 0.2em;
}

.chatbut {
  position: absolute;
  width: 12em;
  height: 2.5em;
  left: calc(50% - 6em);
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #1f707e;
  box-shadow: inset 0px 0px 40px 2px #00d9ff, inset 0px 0px 10px 1px #fff;
  border-radius: 10em;
  color: #89cfdc;
  margin-top: 0.5em;
  font-weight: 600;
  font-size: 1.1em;
  letter-spacing: 1px;
